export default ".addressContainer {\n  width: 60%;\n  background: #f5faff;\n  padding: 25px;\n}\n@media screen and (min-device-width: 320px) and (max-device-width: 768px) {\n  .addressContainer {\n    width: 100%;\n  }\n}\n.addressContainer h4 {\n  color: #007bff;\n  font-size: 15px;\n  font-weight: 700;\n}\n.addressContainer .form-control {\n  padding: 1.375rem 0.75rem !important;\n  border-radius: 0!important;\n  margin-bottom: 5px;\n}\n.addressContainer button.btn {\n  border-radius: 0!important;\n  min-height: 45px;\n  margin-bottom: 5px;\n}\nbutton.location i {\n  font-size: 20px;\n}\nbutton.location span {\n  position: relative;\n  bottom: 3px;\n}\nbutton.action {\n  font-weight: 600;\n}\nbutton.action.save {\n  width: 40%;\n  margin-right: 35px;\n}\nbutton.cancel {\n  background: transparent;\n  color: #007bff;\n}\ndiv.error {\n  color: #ff0000d4;\n  font-size: 14px;\n}\n@media only screen and (max-width: 455px) {\n  button.action {\n    width: 100%!important;\n  }\n}\n/* Custom Radio Button Start*/\n.radiotextsty {\n  color: #707273;\n  font-size: 18px;\n}\n.customradio {\n  display: block;\n  position: relative;\n  padding-left: 30px;\n  margin-bottom: 0px;\n  cursor: pointer;\n  font-size: 18px;\n  -webkit-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n  user-select: none;\n}\n/* Hide the browser's default radio button */\n.customradio input {\n  position: absolute;\n  opacity: 0;\n  cursor: pointer;\n}\n/* Create a custom radio button */\n.checkmark {\n  position: absolute;\n  top: 0;\n  left: 0;\n  height: 22px;\n  width: 22px;\n  background-color: white;\n  border-radius: 50%;\n  border: 1px solid #BEBEBE;\n}\n/* On mouse-over, add a grey background color */\n.customradio:hover input ~ .checkmark {\n  background-color: transparent;\n}\n/* When the radio button is checked, add a blue background */\n.customradio input:checked ~ .checkmark {\n  background-color: white;\n  border: 1px solid #BEBEBE;\n}\n/* Create the indicator (the dot/circle - hidden when not checked) */\n.checkmark:after {\n  content: \"\";\n  position: absolute;\n  display: none;\n}\n/* Show the indicator (dot/circle) when checked */\n.customradio input:checked ~ .checkmark:after {\n  display: block;\n}\n/* Style the indicator (dot/circle) */\n.customradio .checkmark:after {\n  top: 2px;\n  left: 2px;\n  width: 16px;\n  height: 16px;\n  border-radius: 50%;\n  background: #007bff9c;\n}\nlabel.labeltext {\n  color: #A5A4BF;\n}\n#autocomplete {\n  width: 100%;\n  height: 100%;\n  padding: 10px 10px;\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"